.footer__container{
    background: var(--azuloscuro);
    color: var(--white);
}

.footer__content{
    width: 100%;
    max-width: 1300px;
    margin: 0 auto;
    padding: 6rem 0;
}

.footer__contacto{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 4rem;
}

.footer__text{
    width: 48%;
}

.footer__text h5{
    font-size: 5rem;
    font-weight: 800;
    line-height: 5rem;
    margin-bottom: 3rem;
}

.footer__text hr{
    width: 5rem;
    height: 5px;
    background: var(--white);
    border: none;
    margin-bottom: 2rem;
}

.footer__form{
    width: 48%;
}

.footer__form input,
.footer__form textarea{
    max-width: 100%;
    min-width: 100%;
    font-family: poppins;
    font-size: 1.4rem;
    outline: none;
    padding: .5rem;
