}

.footer__form input:-ms-input-placeholder, .footer__form textarea:-ms-input-placeholder{
    color: var(--naranja);
}

.footer__form input::-ms-input-placeholder, .footer__form textarea::-ms-input-placeholder{
    color: var(--naranja);
}

.footer__form input::placeholder,
.footer__form textarea::placeholder{
    color: var(--naranja);
}

.footer__form input{
    margin-bottom: 2rem;
}

.footer__form textarea{
    min-height: 100px;
    color: var(--white);
}

.footer__main{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.footer__redes ul{
    list-style: none;
    display: flex;
}

.footer__redes ul li:not(:last-child){
    margin-right: 2rem;
}

.footer__redes ul li a svg{
    font-size: 2.5rem;
    color: var(--white);
}

.footer__redes ul li:nth-child(1):hover a svg{
    color: var(--facebook);
}

.footer__redes ul li:nth-child(2):hover a svg{
    color: var(--instagram);
}

.footer__redes ul li:nth-child(3):hover a svg{
    color: var(--github);
}

