.ml__cards{
    width: 100%;
    max-width: 900px;
    margin: 2rem auto 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-template-rows: minmax(400px, auto);
    grid-auto-flow: dense;
    grid-gap: 10px;
    position: relative;
}

.ml__card{
    color: var(--white);
    position: relative;
