    bottom: 0;
    left: 0;
    width: 100%;
    height: 30rem;
    background-image: linear-gradient(to top, var(--azuloscuro), transparent);
    display: block;
    z-index: 2;
}

.ml__card:nth-child(1){
    grid-column: span 1;
    grid-row: span 2;
}

.ml__card:nth-child(2){
    grid-column: span 1;
    grid-row: span 1;
}

.ml__card:nth-child(3){
    grid-column: span 1;
    grid-row: span 1;
}

.ml__card img{
    position: relative;
    height: 100%;
